<template>
    <div class="like-box">
        <div class="contents">
            <div class="content" v-for="(like,i) in likes" :key=" 'like'+i" @click="getItemId(like.tradeItemId,like)">
                <div class="likeInfo-img"><img :src="like.img" alt="" /></div>
                <div class="likeInfo">
                    <div class="likeInfo-title">{{like.title}}</div>
                    <div class="likeInfo-price">
                        <div class="price-wrap">
                            <div class="price">
                                <em>¥</em>{{like.price}}
                            </div>
                        </div>
                        <div class="cfav">
                          <div>{{like.cfav}}</div>
                          <StarComponents class="collection"></StarComponents>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import StarComponents from './StarComponents';
export default {
  data(){
    return {
        guesslikes:""
    }
  },
  components:{
    StarComponents
  },
  props:["likes"],
  methods:{
    getItemId(tradeItemId,i){
      localStorage.setItem(tradeItemId,JSON.stringify(i))
      this.$router.push({path:"/detail",query:{tradeItemId}});

    }
  }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.like {
  &-box {
    box-sizing: border-box;
    margin-bottom:100px;
    .contents {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      width: 100%;
      .content {
        width: 46%;
        margin: 2% 0 0 2%;
        > .likeInfo-img {
          width: 100%;
          height: 350px;
          overflow: hidden;
          border-radius: 10px;
          background: #e5e5e5;
          >img{
            width:100%;
            height:100%;
          }
        }
        > .likeInfo {
          height: 120px;
          padding: 10px;
          > .likeInfo-title {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            font-size:24px;
            line-height:40px;
          }
          > .likeInfo-price {
            display: flex;
            justify-content:space-between;
            font-size: 26px;
            line-height: 70px;
            height:70px;
            > .price-wrap {
              display: flex;
            }
            > .cfav {
             display:flex;  
            }
          }
        }
      }
    }
  }
}
</style>



