<template>
    <div class=type-detail>
        <div class="type-right">
            <div v-for="(v,i) of typeRight" :key=" 'list' +i" class="type1">
                <div class="typeImg">
                    <img :src="v.image" alt="" />
                </div>
                <div class="typeTit">{{v.title}}</div>
            </div>
        </div>
        <type-details :sortTit="type.sortTit" :sortPop="type.sortPop"></type-details>
    </div>

</template>
<script>
import TypeDetails from './TypeDetails'
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    components:{
        TypeDetails
    },
    props: ["typeRight"],
    computed: {
        ...mapState(["type"])
    },
     methods: {
        ...mapActions(["loadTypeRightDatas","loadTypeRightThreeDatas"])
     }
};
</script>
<style lang='less'>
@import url("../less/less-var.less");
.type {
    &-detail {
        .type-right {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            padding-top: 50px;
            box-sizing: border-box;
            
            > div {
                width: 25%;
                height: 280px;
                margin-right: 10px;
                margin-left: 30px;
                img {
                    width: 100%;
                    height: 150px;
                }
                .typeTit {
                    margin-top: 20px;
                    text-align: center;
                    font-size: 30px;
                }
            }
        }
    }
}
</style>



