<template>
  <div class="search-all">
    <div class="search">
      <a class="arr">
        <i class="iconfont icon-xiangzuojiantou" @click="topPage"></i>
      </a>
      <input v-show="!flag" class="user-input" type="text" @focus="flag=true" v-model="userInput" :placeholder="q" ref="holder">
      <input v-show="flag" class="user-input" type="text" @focus="flag!=true" v-model="userInput" :placeholder="q" ref="holder">
      <a href="javascript:;" class="search-enter">

      </a>
      <i v-show="!flag" class="iconfont icon-gouwuche"></i>
      <span v-show="flag">搜索</span>
    </div>
    <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
      <div v-show="flag" class="search-content">
        <div class="search-box">
          <history-search></history-search>
          <hot-search :hotSearch="search.hotList.searchList"></hot-search>
        </div>
      </div>
    </transition>
    <div v-show="!flag">
      <search-content :searchTitle="search.titleList"></search-content>
      <scroll-view class="wrapper wrapperSearch">
        <div class="content">
          <gess-like :likes="search.likes"></gess-like>
        </div>
      </scroll-view>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import HotSearch from "../components/searchComponents/HotSearch";
import HistorySearch from "../components/searchComponents/HistorySearch";
import SearchContent from "../components/searchComponents/SearchContent";
import GessLike from "../components/GessLike";
export default {
    data() {
        return {
            flag: false,
            userInput: "",
            q:""
        };
    },
    components: {
        HotSearch,
        HistorySearch,
        SearchContent,
        GessLike
    },
    computed: {
        ...mapState(["search"])
    },
    methods: {
        ...mapActions(["loadSearchAllDatas"]),
        topPage(){
            this.$router.back()
        }
        // shopCart(){

        // }
    },
    mounted() {
        this.q=this.$route.query.q
        console.log(this.$route.query.q);
        this.loadSearchAllDatas({
            cKey: 46,
            page: 1,
            q: this.q,
            sort: "pop"
        });
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.search {
    &-all {
        box-sizing: border-box;
        .search {
            height: 80px;
            /* 弹性盒子 */
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: center;
            border-bottom: 2px solid @gray;
            a {
                text-decoration: none;
                display: inline-block;
                // flex-grow: 1;
                height: 100%;
                padding: 0 15px;
                i {
                    font-size: 40px;
                    line-height: 80px;
                    text-align: center;
                }
            }
            > i {
                font-size: 56px;
                padding: 10px 10px 10px 30px;
            }
            span {
                font-size: 32px;
                line-height: 80px;
                flex-grow: 1;
                text-align: center;
            }
            .user-input {
                flex-grow: 4;
                margin: 14px 0px;
                background-size: 40px;
                text-indent: 40px;
            }
            input:nth-of-type(1) {
                background: url("../assets/search.png") no-repeat left;
                background-size: 60px;
                border: 2px solid #fff;
                box-sizing: border-box;
                background-color: lightcyan;
            }
            input:nth-of-type(2) {
                border: 2px solid #f9446b;
                border-radius: 12px;
                background-color: @white;
            }
        }
       .wrapperSearch{
            box-sizing: border-box;
            position: absolute;
            top: 170px;
            bottom: 100px;
            left: 0px;
            right: 0px;
            overflow: hidden;
       }
    }
}

</style>



