<template>
    <div class="hot-box">
        <div class="topline">热门分类</div>
        <div class="contents">
            <div class="content" v-for="(list,i) in hotType" :key=" 'list'+i">
                <img :src="list.image" alt="" />
                <p>{{list.title}}</p>
            </div>
        </div>
        <div class="toplike">猜你喜欢</div>
    </div>
</template>

<script>
export default {
    props: ["hotType"]
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.hot {
    &-box {
        box-sizing: border-box;
        height: 790px;
        .topline {
            background: url("../assets/topline.png") no-repeat center;
            text-align: center;
            font-size: 32px;
        }
        .contents {
            display: flex;
            width: 100%;
            margin-top: 30px;
            flex-wrap: wrap;
            .content {
                width: 25%;
                > img {
                    width: 80%;
                    height: 120px;
                    margin-left: 10%;
                }
                > p {
                    text-align: center;
                    margin-top: 0px;
                }
            }
        }
        .toplike {
            background: url("../assets/topline.png") no-repeat center;
            text-align: center;
            font-size: 32px;
            margin-top:30px;
        }
    }
}
</style>



