<template>
    <div class="details-top">
        <div class="content" v-for="(v,i) in detailsLikes " :key=" 'list'+i">
            <div class="likeInfo-img"><img :src="v.img" alt="" /></div>
            <div class="likeInfo">
                <div class="likeInfo-title">{{v.title}}</div>
                <div class="likeInfo-price">
                    <div class="price">
                        <span>￥</span>
                        <strong>{{v.price | addZero}}</strong>
                    </div>
                    <div class="orgPrice">
                        <div>￥{{v.orgPrice | addZero}}</div>
                        <div>{{v.price/v.orgPrice*100}}折</div>
                    </div>
                </div>
                <div class="place">
                    <div>免邮费</div>
                    <div>广东省常州市</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props:["detailsLikes"]
};
</script>

<style lang='less'>
@import url("../../less/less-var");
.details {
  &-top {
    > .content {
      margin: 0 20px;
      .likeInfo-img {
        width: 100%;
        img {
          width: 100%;
        }
      }
      .likeInfo {
        .likeInfo-title {
          margin-top: 20px;
        }
        .likeInfo-price {
          display: flex;
          height: 50px;
          margin-bottom: 50px;
          .price {
            font-size: 50px;
            margin-right: 10px;
            span {
              font-size: 30px;
              margin-top: 20px;
            }
          }
          .orgPrice {
            font-size: 24px;
            div:nth-child(1) {
              text-decoration: line-through;
            }
          }
        }
        .place {
          display: flex;
          > div:nth-child(2) {
            margin-left: 40%;
          }
        }
      }
    }
  }
}
</style>



