<template>
        <div ref="wrapper">
            <div class="content">
                <slot></slot>
            </div>
        </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
    mounted() {
        this.$nextTick(() => {
            new BScroll(this.$refs.wrapper, {
                click: true
            });
        });
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
</style>



