<template>
    <div class="type-left">
        <div v-for="(v,index) of typeList" :key=" 'list' +index" v-bind:class="{active:index==isActive}" @click="getData(v.maitKey,v.miniWallkey);checkedItem(index)">
            <p>{{v.title}}</p>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {
            isActive:0
        };
    },
    props: ["typeList"],
    computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadTypeRightDatas","loadTypeRightThreeDatas"]),
        getData(e,f) {
            console.log(e);
            this.pid = e;
            this.fcid=f;
            this.loadTypeRightDatas(e);
            this.loadTypeRightThreeDatas({fcid:f});
        },
        checkedItem(index) {
            console.log(index);
            this.isActive = index;
        }
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.type {
    &-left {
        width: 200px;
        font-size: 36px;
        color: #666666;
        background-color: #ccc;
        > div {
            height: 130px;
            width: 200px;
            text-align: center;
            position: relative;
            font-size: 36px;
            color: #666666;
            > p {
                position: absolute;
                width: 80%;
                height: 50px;
                top: 50%;
                left: 50%;
                margin-top: -25px;
                margin-left: -90px;
                line-height: 50px;
            }
        }
        > .active {
            background-color: @white;
            > p {
                color: #ff5777;
                border-left: solid #ff5777 3px;
            }
        }
    }
}
</style>



