<template>
    <div class="type-detail">
        <div class="sortHeader">
            <div v-for="(item,i) of search.sortTit" :key=" 'list'+i" @click="change(type.sortFcid,item.sortKey)">{{item.title}}</div>
        </div>
        <div class="sortContents">
            <div v-for="(v,i) of search.sortPop" :key=" 'list'+i">
                <div class="sortImg">
                    <img :src="v.img" alt="">
                </div>
                <div class="sortContent">
                    <div class="sortTitle">{{v.title}}</div>
                    <div class="sortPrice">
                        <div>￥{{v.price}}</div>
                        <div class="sortStar">
                            <div>{{v.sale}}</div>
                            <StarComponents class="collection"></StarComponents>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import StarComponents from './StarComponents';
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  components:{
    StarComponents
  },
  props: ["sortTit","sortPop"],
   computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadTypeRightDatas","loadTypeRightThreeDatas"]),
        change(e,f) {
            console.log(e,f);
            this.fcid=e;
            this.sort = f;
            this.loadTypeRightThreeDatas({fcid:e,sort:f});
        }
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.type {
  &-detail {
    > .sortHeader {
      width: 100%;
      display: flex;
      justify-content: space-around;
      font-size: 36px;
      border-top: 1px solid #666666;
      border-bottom: 1px solid #666666;
      padding: 30px 0;
      > div:first-child {
        color: #ef4562;
      }
      > div:nth-child(2) {
        border-left: 1px solid #666666;
        border-right: 1px solid #666666;
        width: 33%;
        text-align: center;
      }
    }
    > .sortContents {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      > div {
        width: 48%;
        > .sortImg {
          height: 400px;
          > img {
            height: 100%;
          }
        }
        > .sortContent {
            padding:0 15px;
            margin-bottom:30px;
          >.sortTitle {
            width: 100%;
            height: 50px;
            line-height: 50px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
          >.sortPrice {
            display: flex;
            justify-content: space-between;
            font-size: 24px;
            >.sortStar{
                 display: flex;
                 >.collection{
                     margin-top:-20px;
                 }
            }
            }
        } 
        }
    }
  }
}
</style>



