import api from '../../server/api';
const state = {
    warpList: [],
    types: [],
    bebefit: [],
    hotType: [],
    likes: [],
}
const actions = {
    loadHomeDatas({ commit }) {
        api.loadDataByPids([122224, 106930, 51833, 51836], (data) => {
            console.log(data)
            commit("setWarpList", data.data["122224"].list)
            commit("setTypes", data.data["106930"].list)
            commit("setBebefit", data.data["51833"].list)
            commit("setHotType", data.data["51836"].list)
        }),
        api.loadlike({
            pid: "9750",
            sort: "pop",
            cKey: 46
            }, (data) => {
                console.log(data)
            commit("setLikes", data.result.wall.docs)
        })
    },
}

const mutations = {
    setWarpList(state, data) {
        state.warpList = data;
    },
    setTypes(state, data) {
        state.types = data;
    },
    setBebefit(state, data) {
        state.bebefit = data;
    },
    setHotType(state, data) {
        state.hotType = data;
    },
    setLikes(state, data) {
        state.likes = data;
    },
}

export default {
    state,
    // getters,
    actions,
    mutations
}


