<template>
    <div class="history">
        <div class="top">
            <div class="left">
                <i class="iconfont icon-sousuo "></i>
                <span>历史搜索</span>
            </div>
            <div class="right">
                <i class="iconfont icon-lajixiang" @click="deletes"></i>
            </div>
        </div>
        <div ref="emptys" class="empty">
            <div v-for="(data,i) in historyList" :key=" 'historyList'+i" class="historyList">
                <span @click="historySearch(data)">{{data}}</span>
            </div>

        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            historyList:[]
        }
    },
    methods:{
        deletes(){
            localStorage.removeItem("historyWord");
            this.$ref.emptys.innerHTML=""
        }
    },
    mounted(){
        this.historyList = JSON.parse(localStorage.getItem("historyWord"));
    }
};
</script>

<style lang='less'>
@import url("../../less/less-var");
.history {
    border-top: 1px solid #ccc;
    > .top {
        display: flex;
        justify-content: space-between;
        padding: 30px;
        font-size: 30px;
    }
    .content {
        margin-left: 70px;
        font-size: 30px;
        margin-bottom: 30px;
    }
    .empty{
        display:flex;
        flex-wrap:wrap;
        padding:0 30px;
        .historyList{
        width:100px;
        height:40px;
        line-height:40px;
    }
    }
    
}
</style>



