
import api from '../../server/api';
const state = {
    rateTags: [],
    commentsList: [],
    total: ""
}
const actions = {
    loadCommentDatas({ commit }, itemId) {
        commit("setcommentsList", []);
        commit("setrateTags", []);
        commit("settotal", "");
        api.loadcomment({appPlat:"m",itemId,type:2}, (data) => {
            console.log("==>", data)
            commit("setrateTags", data.data.rateTags);
            commit("setcommentsList", data.data.list);
            commit("settotal",data.data.total);
        })
    }

};

const mutations = {
    setcommentsList(state, data) {
        state.commentsList = data;
    },
    setrateTags(state, data) {
        state.rateTags = data;
    },
    settotal(state, data) {
        state.total = data;
    },
}

export default {
    state,
    actions,
    mutations
}


