<template>
    <div class="search-content">
        <div class="searchHeader">
            <div v-for="(item,i) of searchTitle" :key=" 'list'+i" class="sortHeadList" :class="{activeList:list==i}" @click="change(i,item.sortKey)">
                {{item.title}}
            </div>
        </div>
    </div>
</template>


<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {
            list: 0,
            sortKey: ""
        };
    },
    props: ["searchTitle"],
    computed: {},
    methods: {
        ...mapActions(["loadSearchAllDatas"]),
        change(i, e) {
            this.list = i;
            this.sortKey = e;
            this.loadSearchAllDatas({
                cKey: 46,
                page: 1,
                q: this.$route.query.q,
                sort: this.sortKey
            });
        }
    },
    mounted() {}
};
</script>

<style lang="less">
@import url("../../less/less-var");

.search {
    &-content {
        margin: 0px 10px;
        border-bottom:1px solid #e6e6e6;
        > .searchHeader {
            display: flex;
            justify-content: space-around;
            height:80px;
            line-height:80px;
            >div:nth-child(2){
                border-left:1px solid #e6e6e6;
                border-right:1px solid #e6e6e6;
                width:200px;
                height:50px;
                line-height: 50px;
                margin-top:10px;
                text-align:center;
            }
            .activeList {
                color: @red;
            }
        }
    }
}
</style>


