<template>
    <div class="category-box">
        <top-nav-bar></top-nav-bar>
        <scroll-view class="wrapper wrapper-left">
            <type-left :typeList="type.typeList"></type-left>
        </scroll-view>
         <scroll-view class="wrapper wrapper-right">
              <type-right :typeRight="type.typeRight"></type-right>
         </scroll-view>
        <bottom-nav></bottom-nav>
    </div>
</template>
<script>
import TopNavBar from "../components/TopNavbar";
import TypeLeft from "../components/TypeLeft";
import TypeRight from "../components/TypeRight";
import BottomNav from "../components/BottomNav";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    components: {
        TopNavBar,
        TypeLeft,
        TypeRight,
        BottomNav
    },
    computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadTypeDatas","loadTypeRightDatas","loadTypeRightThreeDatas"])
    },
    mounted() {
        this.loadTypeDatas();
        this.loadTypeRightDatas();
        this.loadTypeRightThreeDatas({});
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.category {
    &-box {
        .wrapper {
            box-sizing: border-box;
            position: absolute;
            top: 80px;
            bottom: 100px;
            left: 0px;
            right: 0px;
            overflow: hidden;
            // width:25%;
        }
        .wrapper-left{
            width:25%;
        }
         .wrapper-right{
            margin-left: 25%;
        }
    }
}
</style>


