<template>
    <div class="details-page">
        <div class="goback">
            <i class="iconfont icon-xiangzuojiantou" @click="changePage()"></i>
        </div>
        <scroll-view class="wrapper">
            <div class="content">
                <details-top :detailsLikes="detailsLikes"></details-top>
                <details-comment :commentsList="detail.commentsList" :rateTags="detail.rateTags" :total="detail.total"></details-comment>
            </div>
        </scroll-view>
        <details-bottom-nav-bar></details-bottom-nav-bar>
    </div>
</template>

<script>
import DetailsTop from "../components/DetailsComponents/DetailsTop";
import DetailsComment from "../components/DetailsComponents/DetailsComment";
import DetailsBottomNavBar from "../components/DetailsComponents/DetailsBottomNavBar";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  components: {
    DetailsTop,
    DetailsComment,
    DetailsBottomNavBar
  },
  data() {
    return {
      detailsLikes: []
    };
  },
  computed: {
    ...mapState(["detail"])
  },
  methods: {
    ...mapActions(["loadCommentDatas"]),
    getdate() {
      let detailsLike = JSON.parse(localStorage.getItem(this.$route.query.tradeItemId));
      this.detailsLikes.push(detailsLike);
      this.loadCommentDatas(detailsLike.tradeItemId);
    }
  },
  mounted() {
    this.getdate();
  }
};
</script>

<style lang='less'>
@import url("../less/less-var");
.details {
  &-page {
    padding: 0;
    margin: 0;
    .wrapper {
      position: absolute;
      left: 0;
      right: 0;
      top: @topHeight; /* px */
      bottom: @topHeight; /* px */
      overflow: hidden;
    }
    .goback {
      height: @topHeight; /* px */
      line-height: @topHeight; /* px */
      position: fixed;

      i {
        font-size: 50px; /* px */
        margin-left: 40px; /* px */
      }
    }
  }
}
</style>



