<template>
  <div class="top-nav-box">
    <div class="search">
      <i v-show="!flag" class="iconfont icon-xiaoxizhongxin"></i>
      <i v-show="flag" class="iconfont icon-xiangzuojiantou" @click="flag=false"></i>
      <input v-show="!flag" class="user-input" type="text" @focus="flag=true" v-model="userInput" :placeholder="search.hotList.keyWord" ref="holder">
      <input v-show="flag" class="user-input" type="text" @focus="flag=true" v-model="userInput" :placeholder="search.hotList.keyWord">
      <i v-show="!flag" class="iconfont icon-gouwuche"></i>
      <span v-show="flag" @click="searchContent">搜索</span>
    </div>
    <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown" id="cont">
      <div v-show="flag" class="search-content">
        <div class="search-box">
          <history-search :historyList="history"></history-search>
          <hot-search :hotSearch="search.hotList.searchList"></hot-search>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import HotSearch from "../components/searchComponents/HotSearch";
import HistorySearch from "../components/searchComponents/HistorySearch";
export default {
  data() {
    return {
      flag: false,
      userInput: "",
      history: []
    };
  },
  components: {
    HotSearch,
    HistorySearch
  },
  computed: {
    ...mapState(["search"])
  },
  methods: {
    ...mapActions(["loadSearchDatas"]),
    searchContent(){
      if (this.userInput == "") {
        var placeholder = this.$refs.holder.getAttribute("placeholder");
        this.userInput = placeholder;
      }
      this.history.push(this.userInput);
      localStorage.setItem("historyWord", JSON.stringify(this.history));
      this.$router.push({path: "/search",query: { q: this.userInput, userInput: this.userInput}});
    
    },

    // shopCart(){
    //   this.$root.$emit("loadCart",this.selectIndex);
    //   this.$router.push({
    //     path:"buy"
    //   })
    // },
    loadkey() {
        let sea = localStorage.getItem("historyWord") || "[]";
        console.log("==",sea);
        this.history = JSON.parse(sea);
    }
  },
  mounted() {
    this.loadSearchDatas();
    this.loadkey();
  }
};
</script>

<style lang='less'>
@import url("../less/less-var");
.top-nav {
  &-box {
    box-sizing: border-box;
    .search {
      height: 80px;
      /* 弹性盒子 */
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: center;
      border-bottom: 2px solid @gray;
      i {
        font-size: 52px;
        line-height: 80px;
        flex-grow: 1;
        text-align: center;
      }
      span {
        font-size: 32px;
        line-height: 80px;
        flex-grow: 1;
        text-align: center;
      }
      .user-input {
        flex-grow: 5;
        margin: 14px 0px;
        background-size: 40px;
        text-indent: 50px;
      }
      input:nth-of-type(1) {
        background: url("../assets/search.png") no-repeat left;
        background-size: 60px;
        border: 2px solid #fff;
        box-sizing: border-box;
        background-color: lightcyan;
      }
      input:nth-of-type(2) {
        border: 2px solid #f9446b;
        border-radius: 12px;
        background-color: @white;
      }
    }
    .search-box {
      background-color: white;
      position: absolute;
      top: 80px;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 999;
      overflow: hidden;
      color: #999;
    }
  }
}
</style>



