<template>
    <div class="detail-comment">
        <div class="buycomment">
            买家评论
            <span>{{total}}</span>
            <div class="sale" v-if="!flag">|销售
                <span>12339</span>
            </div>
        </div>
        <div class="tags" v-if="rateTags.length!=0">
            <span v-for="(v,i) in rateTags" :key=" 'item' +i" :class="v.emotion=='positive'?'tagPositive':'tagNegative'">{{v.value}}({{v.num}})</span>
        </div>
        <div class="comment" v-if="commentsList.length==0">暂无评价</div>
        <div class="commentsInfo">
            <div class="oneInfo" v-for="(list ,i) in commentsList" :key=" 'list'+i ">
                <div class="userTitle">
                    <div class="userImg">
                        <img :src="list.user.avatar" alt="">
                    </div>
                    <div class="userComment">
                        <span>{{list.user.uname}}</span>
                    </div>
                </div>

                <div class="userContent">{{list.content}}</div>
                <div class="userTime">{{list.time}}</div>
            </div>
        </div>
    </div>

</template>

<script>
export default {
  data() {
    return {
      flag: false
    };
  },
  props: ["commentsList", "rateTags", "total"]
};
</script>

<style lang='less'>
@import url("../../less/less-var.less");
.detail {
  &-comment {
    margin-top: 30px;
    padding: 0 30px;
    padding-bottom:100px;
    .buycomment {
      display: flex;
      height: 80px;
      line-height: 80px;
    }
    .tags {
      display: flex;
      padding-left: 30px; /* px */
      padding-right: 30px; /* px */
      flex-wrap: wrap;
      .tagPositive {
        color: #f9446b;
        background: rgb(255, 232, 238);
        padding: 10px 10px; /* px */
        margin-right: 10px; /* px */
        margin-bottom: 10px; /* px */
      }
      .tagNegative {
        color: #2c3e50;
        background: #f6f6f6;
        padding: 10px 10px; /* px */
        margin-right: 10px; /* px */
        margin-bottom: 10px; /* px */
      }
    }
    .commentsInfo {
      padding: 0 30px;
      > .oneInfo {
        width: 100%;
        margin-bottom:20px;
        > .userTitle {
            display:flex;
          > .userImg {
            width: 100px;
            height: 100px;
            img {
              width: 100%;
              border-radius: 50px;
              border: 1px solid #efefef;
            }
          }
          .userComment {
            // display: inline-block;
            line-height:100px;
            font-size:30px;
          }
        }
        .userContent{
            margin-top:30px;
            margin-bottom:30px;
            line-height:40px;
        }
      }
    }
  }
}
</style>



