<template>
    <div class="clearSize">
        <img v-show="flag" :src="openStarUrl"  @click="flag=!flag"/>
        <img v-show="!flag" :src="closeStarUrl" @click="flag=!flag"/>
    </div>
</template>

<script>
export default {
     data() {
        return {
            flag: false,
            openStarUrl:"static/img/star-open.png",
            closeStarUrl:"static/img/star-close.png"
        }
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
</style>



