<template>
    <div class="hotSearch">
        <div class="hotSearch-title">
            <i class="iconfont icon-shoucang"></i>
            <span>热门搜索</span>
        </div>
        <div class="hot-search">
            <div class="hotContent">
                <div v-for="(v,i) in hotSearch " :key=" 'hot'+i" :class=" 'hotRed'+i" @click="HotList(v.frontword)">
                    {{v.frontword}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ["hotSearch"],
    methods:{
        HotList(e){
            let h = JSON.parse(localStorage.getItem("historyWord"));
            h.push(e);
            localStorage.setItem("historyWord",JSON.stringify(h)); 
            // let key=localStorage.getItem("key");
            this.$router.push({path:"/search",query:{q:e}});
            


        },
    }
};
</script>

<style lang='less'>
@import url("../../less/less-var.less");
 .hotSearch {
    border-top: 1px solid #ccc;
    padding-top: 20px;
    > .hotSearch-title {
        padding-left: 30px;
    }
    .hot {
        &-search {
            > .hotContent {
                display: flex;
                flex-wrap: wrap;
                // justify-content:space-around;
                padding: 30px 70px 0 70px;
                > div {
                    width: 80px;
                    margin-right: 40px;
                    margin-bottom: 30px;
                    height: 50px;
                    line-height: 50px;
                    font-size:24px;
                    text-align: center;
                    border-radius: 6px;
                    border: 1px solid #999;
                }
                >.hotRed4,.hotRed6,.hotRed9{
                    color:#ff4466;
                }
            }
        }
    }
}
</style>



