import api from '../../server/api';
const state = {
    typeList:[],
    typeRight:[],
    sortPop:[],
    sortTit:[],
    sortFcid:[]
}
const actions = {
    loadTypeDatas({ commit }){
        api.loadtypeLeft({
            appPlat:"m",
            pids:117330   
            }, (data) => {
                console.log(data)
            commit("setTypeList", data.data[117330].list)
        })
    },
    loadTypeRightDatas({ commit },pid=3627){
        api.loadtypeRight({
            appPlat:"m",
            pid:pid  
            }, (data) => {
                console.log(data.data.list)
            commit("setTypeRight", data.data.list)
        })   
    },
    loadTypeRightThreeDatas({ commit },{fcid=41888,sort="pop"}){
        api.loadSortPop({
            fcid:fcid,
            page:1,
            sort:sort,
            cKey:"h5-cube"
            }, (data) => {
                console.log(data)
            commit("setloadSortPop", data.result.wall.docs);
            commit("setloadSortTit",data.result.sortFilter);
            commit("setloadSortFcid",data.result.param.fcid);
        })   
    },
}

const mutations = {
    setTypeList(state, data) {
        state.typeList = data;
    },
    setTypeRight(state, data) {
        state.typeRight = data;
    },
    setloadSortPop(state, data) {
        state.sortPop = data;
    },
    setloadSortTit(state, data) {
        state.sortTit = data;
    },
    setloadSortFcid(state, data) {
        state.sortFcid = data;
    },
}

export default {
    state,
    // getters,
    actions,
    mutations
}


