<template>
    <div class="swiper-box">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(v,i) of swiperList" :key=" 'swiper'+i">
                <img :src="v.image_800" alt="">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    data() {
        return {
            swiperOption: {
                // autoplay:auto,
                autoplay: {
                    disableOnInteraction: false
                },
                pagination: {
                    el: ".swiper-pagination"
                }
            }
        };
    },
    props: {
        swiperList: { type: Array, default: [] }
    }
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.swiper {
  &-box {
    box-sizing: border-box;
    height: 300px;
    width:100%;
    overflow-y: hidden;
    img {
      width: 100%;
    }
    .swiper-pagination-fraction,
    .swiper-pagination-custom,
    .swiper-container-horizontal > .swiper-pagination-bullets {
      bottom: 100px;
      left: 0;
      width: 100%;
    }
    .swiper-pagination-bullet {
      width: 20px;
      height: 20px;
      display: inline-block;
      border-radius: 100%;
      background: white;
      opacity: 0.6;
    }
    .swiper-pagination-bullet-active {
      opacity: 1;
      background: #007aff;
    }
  }
}
</style>



