<template>
    <div class="sale-box">
        <div class="topline">超实惠-促销直达</div>
        <div class="contents">
            <div v-for="(sale,i) in bebefit" :key=" 'sale'+i" class="content">
                <div class="first">
                    <div class="title"> {{sale.title}}</div>   
                    <div class="viceTitle">{{sale.viceTitle}}</div>
                </div>
                <div class="two"><img :src="sale.image" alt=""></div>   
            </div>
        </div>   
    </div>
</template>

<script>
export default {
    props:["bebefit"]
};
</script>

<style lang='less'>
@import url("../less/less-var.less");
.sale{
    &-box{
        box-sizing: border-box;
        height:600px;
        width:100%;
        .topline{
            background:url("../assets/topline.png") no-repeat center;
            text-align: center;
            font-size:32px;
        }
        .contents{
            display:flex;
            flex-wrap:wrap;
            margin-top:36px;
            border-top:1px solid lightgray;
            border-bottom:1px solid lightgray;
            .content{
                width:33%;
                height:250px;
                text-align:center;
                .title{
                    font-size:30px;
                    margin-top:20px;
                }
                .viceTitle{
                    color:@red;
                }
               img {
                    vertical-align: middle;
                    height:130px;
                    width:200px;
                    margin-top:20px;
                }
            }
            > div:first-child{
                border-right:1px solid lightgray;
                height:250px;
                width:49%;
            }
            > div:nth-child(2){
                border-bottom:1px solid lightgray;
                height:125px;
                width:49%;
                display:flex;
                img{
                    height:100px;
                    width:100px;
                    margin-top:10px;
                }
                > .first{
                    width:350px;
                }
            }
            > div:nth-child(3){
                height:124px;
                width:49%;
                display:flex;
                margin-left:-354px;
                margin-top:124px;
                > .first{
                    width:350px;
                }
                img{
                    height:100px;
                    width:100px;
                    margin-top:10px;
                }
            }
            > div:nth-child(4){
                border-top:1px solid lightgray;
                border-right:1px solid lightgray;
            }
            > div:nth-child(5){
                 border-top:1px solid lightgray;
                 border-right:1px solid lightgray;
            }
             > div:nth-child(6){
                 border-top:1px solid lightgray;
            }
        }
    }
}
</style>



