<template>
    <div class="home-box">
        <top-nav-bar></top-nav-bar>
        <scroll-view class="wrapper">
            <swiper-component :swiperList="home.warpList"></swiper-component>
            <!-- <p v-for="(v,i) in 50" :key=" 'a'+i">aaaaa</p> -->
            <header-list :types="home.types"></header-list>
            <sale-component :bebefit="home.bebefit"></sale-component>
            <hot-product :hotType="home.hotType"></hot-product>
            <guess-like :likes="home.likes"></guess-like>
        </scroll-view>
        <bottom-nav></bottom-nav>
    </div>

</template>

<script>
import TopNavBar from "../components/TopNavbar";
import SwiperComponent from "../components/SwiperComponent";
import HeaderList from "../components/HeaderList";
import SaleComponent from "../components/SaleComponent";
import HotProduct from "../components/HotProduct";
import GuessLike from "../components/GessLike";
import BottomNav from "../components/BottomNav";
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    components: {
        TopNavBar,
        SwiperComponent,
        HeaderList,
        SaleComponent,
        HotProduct,
        GuessLike,
        BottomNav
    },

    computed: {
        ...mapState(["home"])
    },
    methods: {
        ...mapActions(["loadHomeDatas"])
    },
    mounted() {
        this.loadHomeDatas();
    }

};

</script>

<style lang='less'>
.wrapper {
    box-sizing: border-box;
    position: absolute;
    top: 80px;
    bottom: 100px;
    left: 0px;
    right: 0px;
    overflow: hidden;
}
</style>





