<template>
    <div class="me-box">
        <scroll-view class="wrapper">
            <div class="top">
                <div class="photo"><img src="http://via.placeholder.com/100x100" alt=""></div>
                <div class="myInfo">
                    <div class="username">hahahahahh</div>
                    <div class="address">我的收货地址

                    </div>
                </div>
            </div>
            <div class="InfoLists">
                <div class="InfoList" v-for="(v,i) of InfoList" :key=" 'list'+i">
                    <div class="infoImg"><img :src="v.img" alt=""></div>
                    <div class="infoContent">{{v.name}}</div>
                    <div class="infoRow">
                        <i class="iconfont icon-xiangzuojiantou"></i>
                    </div>
                </div>
                <div class="exit">
                    <input type="button" value="退出登录">
                </div>
            </div>
        </scroll-view>
        <bottom-nav></bottom-nav>
    </div>
</template>
<script>
import BottomNav from "../components/BottomNav";
export default {
  components: {
    BottomNav
  },
  data() {
    return {
      InfoList: [
        { img: "../../static/imgs/order.png", name: "我的订单", action: "" },
        { img: "../../static/imgs/cart.png", name: "我的购物车", action: "" },
        {
          img: "../../static/imgs/group_booking.png",
          name: "我的拼团",
          action: ""
        },
        {
          img: "../../static/imgs/wallet.png",
          name: "我的钱包",
          action: "快速提现"
        },
        { img: "../../static/imgs/coupon.png", name: "我的优惠券", action: "" },
        {
          img: "../../static/imgs/wishlist.png",
          name: "我收藏的商品",
          action: ""
        },
        {
          img: "../../static/imgs/bookmark.png",
          name: "我收藏的店铺",
          action: ""
        },
        { img: "../../static/imgs/news.png", name: "消息通知", action: "" },
        { img: "../../static/imgs/service.png", name: "客服", action: "" },
        { img: "../../static/imgs/feedback.png", name: "意见反馈", action: "" }
      ]
    };
  }
};
</script>
<style lang="less">
@import url("../less/less-var.less");
.me {
  &-box {
    background: #f6f6f6;
    .top {
        padding-top:40px;
      background: white;
      display: flex;
      > .photo {
        width: 135px;
        height: 135px;
        margin: 60px 60px 60px 40px;
        img {
          width: 100%;
        }
      }
      > .myInfo {
        margin-top: 60px;
        height: 50px;
        .username {
          font-size: 57px;
        }
        .address {
          font-size: 36px;
          margin-top: 15px;
          color: #424242;
        }
      }
    }
    .InfoLists {
      margin-top: 20px;
      padding-bottom: 100px;
      .InfoList {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 150px;
        line-height: 150px;
        padding-left: 40px;
        border-bottom: 1px solid #bbbbbb;
        margin-bottom: 20px;
        background: white;
        .infoImg {
          margin-top: 20px;
          flex-grow: 1;
          text-align: center;
          width: 40px;
          height: 40px;
          img {
            width: 100%;
          }
        }
        .infoContent {
          padding-left: 20%;
          font-size: 45px;
          flex-grow: 4;
        }
        .infoRow {
          flex-grow: 1;
        }
      }
    }
    .exit {
      margin-top: 60px;
      margin-left: 150px;
      input {
        width: 500px;
        height: 120px;
        border-radius: 15px;
        text-align: align;
        font-size: 45px;
        border: none;
        background: #f9446b;
        color: white;
      }
    }
  }
}
.wrapper {
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  bottom: 100px;
  left: 0px;
  right: 0px;
  overflow: hidden;
}
</style>


