import api from '../../server/api';
const state = {
    hotList: {},
    titleList: [],
    likes: [],
    hotSearch: []
}
const actions = {
    loadSearchDatas({ commit }) {
        api.loadHotSearch({
            platform: "H5",
            code: "hotWord,sketch"
        }, (data) => {
            console.log("==>", data)
            commit("setHotSearch",{keyWord:data.data.sketch.data.frontword,searchList:data.data.hotWord.data});
        })
    },
    loadSearchAllDatas({ commit }, queryParams = { q, sort: "pop" }) {
        queryParams.cKey = 46;
        queryParams.page = 1;
        commit("setlikes", []);
        api.loadAllSearch(queryParams, (data) => {
            commit("setlikes", data.result.wall.docs);
            commit("settitleList", data.result.sortFilter);
        });
    }

};

const mutations = {
    setHotSearch(state, data) {
        state.hotList = data;
    },
    settitleList(state, data) {
        state.titleList = data;
    },
    setlikes(state, data) {
        state.likes = data;
    }
}


export default {
    state,
    actions,
    mutations
}


