import jsonpUtil from '../util/jsonpUtil'


// 请求地址
const mce_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_seach = "https://list.mogujie.com/search";
const type_left="https://mce.mogucdn.com/jsonp/multiget/3";
const type_right="https://mce.mogucdn.com/jsonp/get/3";
const type_sort="https://list.mogujie.com/search";
const hot_search="https://list.mogujie.com/module/mget";
const all_search=" https://list.mogujie.com/search";
const comment_content=" https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1"

// 参数常量
const pidsParams = {
    appPlat:"m"
} 


const error = function(error){
    alert(error.message);
}

// 请求方法
export default {
    loadDataByPids(pids,success=()=>{}){
        if(!pids){
            throw new Error("请求参数错误");
        }
        if(!Array.isArray(pids)){
            pidsParams.pids = pids;
        }
        if(Array.isArray(pids)){
            let temp = "";
            for (const i in pids) {
                temp += pids[i]+",";
            }
            pidsParams.pids = temp.replace(/,$/,"");
        }
        jsonpUtil.request(mce_multiget,pidsParams)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },

    loadlike(params,success=()=>{}){
        jsonpUtil.request(list_seach,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },

    loadtypeLeft(params,success=()=>{}){
        jsonpUtil.request(type_left,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },
    loadtypeRight(params,success=()=>{}){
        jsonpUtil.request(type_right,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },
    loadSortPop(params,success=()=>{}){
        jsonpUtil.request(type_sort,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },
    //hotsearch
    loadHotSearch(params,success=()=>{}){
        jsonpUtil.request(hot_search,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },
    loadAllSearch(params,success=()=>{}){
        jsonpUtil.request(all_search,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    },
    loadcomment(params,success=()=>{}){
        jsonpUtil.request(comment_content,params)
                 .then((response)=>{
                    if(response.ok){
                        return response.json();
                    }else{
                        throw new Error("请求失败");
                    }
                 })
                 .then(success)
                 .catch(error)
    }



}
